<template>
  <div class="e-demo" :class="[ arrowPosition ? `e-demo__arrow-position-${arrowPosition}` : `e-demo__arrow-position-default`, arrowPositionMobile ? `e-demo__arrow-position-mobile-${arrowPositionMobile}` : `e-demo__arrow-position-mobile-default`, 'e-demo__'+theme ]">

    <div class="e-demo__content">
      <h3 class="e-demo__title">{{title}}</h3>
      <slot></slot>
    </div>

    <div class="e-demo__footer">
      <button class="e-demo__button end" @click="endTour">End Tour</button>

      <div class="e-demo__footer-right">
        <button v-if="demoStep !== 14" class="e-demo__button prev" @click="prevStep" :disabled="demoStep <= 1 || isPrevDisabled">
          <e-icon :src="iconArrow" />
          Prev
        </button>
        <button class="e-demo__button next" :class="{'action': this.nextAction }" @click="nextStep" :disabled="isNextDisabled">
        <span v-if="nextText">
          {{nextText}}
        </span>
          <span v-else>
          Next
        </span>
          <e-icon v-if="demoStep !== 14" :src="iconArrow" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { setStorageValue } from '@/util/storage';
import { setTourStep } from '@/api/backend/statistics';

import EIcon from '@/components/EIcon';

import iconArrow from '@/icons/e-icon__arrow-demo-popup.svg';

export default {
  name: 'e-alert',

  data() {
    return {
      iconArrow,
    };
  },

  props: {
    arrowPosition: {
      type: String,
      default: '',
    },
    arrowPositionMobile: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
    nextText: {
      type: String,
      default: '',
    },
    nextAction: {
      type: Function,
    },

    isNextDisabled: {
      type: Boolean,
      default: false,
    },

    isPrevDisabled: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      theme: 'ui/getTheme',
    }),
  },

  methods: {
    endTour() {
      this.$store.commit('ui/SET_DEMO_STEP', null);
      setStorageValue('demoStep', null);
    },
    prevStep() {
      // if (this.demoStep === 5 && window.innerWidth < 1280) {
      //   this.$store.commit('ui/SET_DEMO_STEP', this.demoStep - 2); // Hide step 4 for 1279px resolution
      //   return;
      // }

      this.$store.commit('ui/SET_DEMO_STEP', this.demoStep - 1);
    },
    nextStep() {
      setTourStep({ walletAddress: this.activeWallet.address, step: this.demoStep });

      if (this.nextAction) {
        this.nextAction();
        return;
      }

      if (this.demoStep === 14) {
        this.endTour();
      } else {
        this.$store.commit('ui/SET_DEMO_STEP', this.demoStep + 1);
      }
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-demo {
  $this: &;

  width: 320px;
  background: #fff;
  border-radius: 3px;
  overflow: hidden;
  text-transform: none;
  text-align: left;

  @include r(1279) {
    /*display: none;*/
  }

  &::before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 8px solid #fff;
    border-bottom: 7px solid transparent;
    position: absolute;
  }

  &.e-demo__arrow-position-default,
  &.e-demo__arrow-position-left {
    &::before {
      top: 12px;
      left: 0;
      transform: translate(-100%, 0);
    }
  }

  &.e-demo__arrow-position-right {
    &::before {
      top: 12px;
      right: 0;
      transform: translate(100%, 0) rotate(180deg);
    }
  }

  &.e-demo__arrow-position-top {
    &::before {
      top: 3px;
      left: 50%;
      transform: translate(-50%, -100%) rotate(90deg);
    }
  }
  &.e-demo__arrow-position-mobile-top {
    &::before {
      @include r(1279) {
        top: 3px;
        left: 50%;
        transform: translate(-50%, -100%) rotate(90deg);
      }
    }
  }
  &.e-demo__arrow-position-mobile-topLeft {
    &::before {
      @include r(1279) {
        top: 4px;
        left: 0;
        transform: translate(50px, -100%) rotate(90deg);
      }
    }
  }

  &.e-demo__arrow-position-bottom {
    &::before {
      bottom: 3px;
      left: 50%;
      transform: translate(-50%, 100%) rotate(270deg);
      border-right-color: #D9D9D9;
    }
  }

  &__content {
    padding: 15px 18px 20px;
    box-sizing: border-box;
    font-size: 13px;
    color: #5c5d61;
  }

  &__title {
    font-weight: 600;
    font-size: 18px;
    color: #232323;
    margin: 0 0 6px;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #D9D9D9;
    box-sizing: border-box;
    padding: 10px 18px;
  }

  &__footer-right {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 12px;
    color: #232323;
    text-transform: uppercase;
    text-align: center;
    line-height: 14px;
    background: #fff;
    border-radius: 3px;
    height: 35px;
    padding: 3px 16px;
    overflow: hidden;
    transition: background-color .15s ease-in-out, color .15s ease-in-out;

    &:hover {
      background: $color-green-dark;
      color: #fff;

      .e-icon svg path {
        fill: #fff !important;
      }
    }

    &:active {
      transform: translate(0, 1px);
    }

    &.action {
      background: $color-green-dark;
      color: #fff;

      .e-icon svg path {
        fill: #fff !important;
      }
    }

    &.prev {
      margin-left: auto;
      margin-right: 4px;

      .e-icon {
        transform: rotate(-180deg);
        margin-right: 8px;
      }

      &:hover {
        background: rgba(255,255,255,.75);
        color: #000;

        .e-icon svg path {
          fill: #000 !important;
        }
      }
    }

    &.next {
      .e-icon {
        margin-left: 8px;
      }
    }

    &:disabled {
      background: rgba(255,255,255,.4);
      color: rgba(#232323, .4);
      cursor: not-allowed;

      .e-icon svg path {
        fill: rgba(#232323, .4) !important;
      }

      &:hover {
        background: rgba(255,255,255,.4);
        color: rgba(#232323, .4);

        .e-icon svg path {
          fill: rgba(#232323, .4) !important;
        }
      }
    }

    &.end {
      &:hover {
        background: $color-red;
      }
    }

    .e-icon {
      width: 6px;
      height: 9px;
      overflow: hidden;
      position: relative;

      svg path {
        fill: #8D9098 !important;
        transition: fill .15s ease-in-out;
      }
    }
  }

  &__light {
    background: #ebebeb;
    color: #fff;

    &::before {
      border-right-color: #EBEBEB;
    }

    #{$this}__title,
    #{$this}__content {
      /*color: #fff;*/
    }
  }
}
</style>



