<template>
  <div
    class="the-header-futures-result"
    :class="{
      'the-header-futures-result__light': theme === 'light',
      'the-header-futures-result__dark': theme === 'dark'
     }"
  >
    <template>
      <div class="the-header-futures-result__message">
        <span>Futures Contracts</span>
      </div>

      <div class="the-header-futures-result__wrapper">
        <div class="the-header-futures-result__search-table">
          <div class="the-header-futures-result__search-table-item the-header-futures-result__search-table-thead">
            <div class="the-header-futures-result__search-table-item-block">Asset</div>
            <div class="the-header-futures-result__search-table-item-block">Price</div>
            <div class="the-header-futures-result__search-table-item-block">Volume 24h</div>
            <!--<div class="the-header-futures-result__search-table-item-block">Open</div>-->
            <!--<div class="the-header-futures-result__search-table-item-block">High</div>-->
            <!--<div class="the-header-futures-result__search-table-item-block">Low</div>-->
            <!--<div class="the-header-futures-result__search-table-item-block">Margin</div>-->
            <!--<div class="the-header-futures-result__search-table-item-block">Expires</div>-->
            <div class="the-header-futures-result__search-table-item-block">Change 24h</div>
          </div>
        </div>

        <div
          class="the-header-futures-result__search-list"
          :class="mobile ? 'the-header-futures-result__search-list_height' : ''"
        >
          <div class="the-header-futures-result__search-table">
            <e-futures-asset-item
              v-for="asset in assetsToDisplay"
              :key="asset.id"
              v-bind="asset"
              @click="handleSelect(asset.recommendedContract.futuresContractHash)"
            />
          </div>
        </div>
      </div>

      <div class="the-header-futures-result__preloader" v-if="notLoaded">
        <e-preloader/>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import throttle from 'lodash/throttle';
import get from 'lodash/get';
import memoize from 'lodash/memoize';
import findIndex from 'lodash/findIndex';
import { BigNumber } from 'bignumber.js';

import iconQuestion from '@/icons/e-icon__question.svg';

import { onFuturesContracts, offFuturesContracts } from '@/api/backend/futures/contracts';
import { getAssets } from '@/api/backend/futures/assets';
import { onTradeChange, removeTradeChangeHandler } from '@/api/backend/futures/trades';
import { ASYNC_STATES, FUTURES_AMOUNT_DECIMALS } from '@/util/constants';

import EIcon from '@/components/EIcon';
import EPreloader from '@/components/EPreloader';
import EFuturesAssetItem from '@/components/EFuturesAssetItem';

/**
 * @property requestState
 */
export default {
  name: 'the-header-futures-result',

  props: {
    mobile: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      assetsToDisplay: [],
      requestState: ASYNC_STATES.INITIAL,
      onFuturesContractsHandler: undefined,
      loadFuturesAssetsThrottleFn: undefined,
      loadFuturesAssetsInterval: null,
    };
  },

  computed: {
    ...mapGetters({
      contractState: 'futuresActiveContract/contractState',
      baseToken: 'balance/baseToken',
      theme: 'ui/getTheme',
    }),

    ...mapState({
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),

    notLoaded() {
      return [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.requestState);
    },
  },

  mounted() {
    this.loadFuturesAssets();

    this.$store.dispatch('tokens/loadTokens')
      .then(() => {
        this.loadFuturesAssetsInterval = setInterval(this.loadFuturesAssets.bind(this), 1000 * 120);
      });

    this.handleNewTrade = (tradeEvt) => {
      const assetName = get(tradeEvt, 'data.futures_asset.name');
      const getAssetIndex = memoize(findIndex.bind(this, this.assetsToDisplay));
      const assetIndex = getAssetIndex(a => a.name === assetName);

      if (this.assetsToDisplay[assetIndex]) {
        this.assetsToDisplay[assetIndex] = { ...this.assetsToDisplay[assetIndex], lastPrice: BigNumber(get(tradeEvt, 'data.price')) };

        const volume = BigNumber(get(tradeEvt, 'data.amount_filled') * get(tradeEvt, 'data.price')).shiftedBy(-FUTURES_AMOUNT_DECIMALS);
        this.assetsToDisplay[assetIndex] = { ...this.assetsToDisplay[assetIndex], volume24h: this.assetsToDisplay[assetIndex].volume24h.plus(volume) };

        this.assetsToDisplay = [...this.assetsToDisplay];
      }
    };

    onTradeChange(throttle(this.handleNewTrade, 500));
  },

  beforeDestroy() {
    this.stopListeningContractChanges();
    clearInterval(this.loadFuturesAssetsInterval);
    removeTradeChangeHandler(this.handleNewTrade);
    this.handleNewTrade = null;
  },

  methods: {
    handleSelect(item) {
      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);
      this.$emit('select', item);
    },

    async loadFuturesAssets() {
      this.requestState = ASYNC_STATES.PENDING;
      const request = getAssets({ contractAddress: this.activeFuturesSmartContract.contractAddress, baseToken: this.baseToken, type: this.type });
      this.actualRequest = request;
      try {
        const assets = await this.actualRequest;
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.SUCCESS;
        this.$store.commit('tokens/SET_ASSETS', assets);

        this.assetsToDisplay = assets.slice(0, 50);
      } catch (err) {
        if (request !== this.actualRequest) return;
        this.requestState = ASYNC_STATES.ERROR;
      }
    },

    loadFuturesAssetsWithThrottle() {
      this.loadFuturesAssetsThrottleFn();
    },

    startListeningContractChanges() {
      this.onFuturesContractsHandler = () => this.loadFuturesAssetsWithThrottle();

      onFuturesContracts(this.onFuturesContractsHandler);
    },

    stopListeningContractChanges() {
      offFuturesContracts(this.onFuturesContractsHandler);
    },
  },

  icons: {
    iconQuestion,
  },

  components: { EIcon, EFuturesAssetItem, EPreloader },
};
</script>

<style lang="scss">
  @import '~@/scss/styles';

  .the-header-futures-result {
    $this: &;

    position: relative;
    height: 100%;
    border-radius: 0 0 5px 5px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background: #F2F6FF;
    box-shadow: 0 15px 15px 0 rgba(17,17,17,0.20);

    &__wrapper {
      margin-right: 3px;
    }

    &__preloader {
      position: relative;
      width: 100%;
      height: 40px;
      margin-bottom: 20px;
    }

    &__rec {
      margin-right: 3px;
    }

    &__search-list {
      min-height: 20px;
      max-height: 232px;
      margin-right: 2px;
      width: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      position: relative;
      border-radius: 0 0 4px 4px;
      margin-bottom: 5px;

      &::-webkit-scrollbar {
        position: relative;
        width: 2px;
        left: -6px;
      }

      &::-webkit-scrollbar-track {
        background: $color-violet;
        border-radius: 1px;
      }

      &::-webkit-scrollbar-thumb {
        background: $color-blue;
        border-radius: 1px;
      }
    }

    &__search-list-message {
      color: #1F2EE9;
      font-size: 12px;
      font-weight: 500;
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -12px);
      background: #fff;
      padding: 0 4px;
    }

    &__search-list_height {
      max-height: 100%;
    }

    &__message {
      flex: 0 0 auto;
      height: 37px;
      padding: 4px 0 2px;
      background-color: #F2F6FF;
      border-radius: 4px 4px 0 0;
      color: $color-dark-blue;
      font-size: 12px;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 0;
      display: flex;
      justify-content: center;
      align-items: center;

      span {
        /*margin-left: 85px;*/
      }
    }

    &__message-small {
      font-size: 10px;
      text-transform: uppercase;
      padding: 4px 0 0;
    }

    &__link {
      margin-left: 5px;
      color: #1F2EE9;
    }

    &__recomend {
      color: rgba(0, 0, 0, 0.4);
      font-size: 13px;
      font-weight: 500;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 20px;
      margin-top: -10px;
      padding-bottom: 10px;
      background-color: #f5f5f5;
      letter-spacing: -.5px;
    }

    &__search-table {
      width: calc(100% - 9px);
      border-collapse: separate;
      border-spacing: 0 1px;
      margin: 0 3px 0 6px;
    }

    &__search-table-thead {
      font-size: 9px;
      font-weight: 500;
      text-transform: uppercase;
      color: #8D9098;
    }

    &__search-table-item {
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    &__search-table-item-placeholder {
      height: 28px;
      background-color: #fff;
      border-bottom: 1px solid #e7e8eb;
    }

    &__search-table-item-block {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding: 4px 8px;
      box-sizing: border-box;
      flex: 1 1 25%;

      @include r(640) {
        padding-left: 6px;
        padding-right: 6px;
      }

      // For open/ high/ log
      // flex: 0 0 10.57%;
      // nth7: 13%
      // nth8: 13%

      &:nth-child(1) {
        justify-content: flex-start;

        @include r(640) {
          flex: 0 0 50px;
        }
      }

      &:nth-child(2) {
        text-align: right;
      }

      &:nth-child(3) {
        /*@include r(640) {
          flex: 0 0 84px;
        }*/
      }

      &:nth-child(4) {
        /*@include r(640) {
          flex: 0 0 84px;
        }*/
      }
    }

    &__how-to-list-token {
      height: 39px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 500;
      color: #0097FE;
      margin-top: 6px;
      cursor: pointer;
      text-decoration: none;
      background-color: #f5f5f5;
      transition: background-color .15s ease-in-out;
    }

    &__how-to-list-token-icon {
      margin-right: 2px;
      width: 40px;
      height: 40px;

      svg {
        width: 40px;
        height: 47px;
        position: relative;
        top: -8px;
      }
    }

    &__placeholder {
      display: block;
      width: 50px;
      height: 8px;
      border-radius: 20px;
      opacity: 1;
      background: #E3E3EE;
    }

    &__dark {
      background: $dark-background;

      #{$this}__message {
        background: $dark-background;
        color: #fff;
      }

      #{$this}__search-table-item-block {
        margin-bottom: 1px;
      }

      #{$this}__message-empty {
        color: $color-dark-grey;
      }

      #{$this}__recomend {
        background: $dark-background;
        color: $color-dark-grey;
      }

      #{$this}__search-results {
        background: $dark-background;
      }

      #{$this}__search-list {
        background: $dark-background;

        &::-webkit-scrollbar-track {
          background-color: #fff;
        }

        &::-webkit-scrollbar-thumb {
          background-color: #979797;
        }
      }

      #{$this}__link {
        color: #0097FE;
      }

      #{$this}__search-list {
        &::-webkit-scrollbar-track {
          background-color: $dark-background;
        }

        &::-webkit-scrollbar-thumb {
          background-color: $color-dark-grey;
        }
      }

      #{$this}__search-table-thead {
        color: $color-dark-grey;
      }

      #{$this}__search-table {
        color: #fff;

        thead {
          color: $color-dark-grey;
        }
      }

      #{$this}__how-to-list-token {
        background: #1a1a1a;

        &:hover {
          background: #232323;
        }
      }

      #{$this}__placeholder {
        background: #1f1f1f;
      }

      #{$this}__search-table-item-placeholder {
        background-color: #181818;
        border-bottom: 1px solid #101010;
      }

      #{$this}__search-list-message {
        color: #8D9098;
        background: #181818;
      }
    }
  }
</style>



