<template>
  <div
    class="the-wallet-new-btc"
    :class="{
      'the-wallet-new-btc__light': theme === 'light',
      'the-wallet-new-btc__dark': theme === 'dark'
    }"
  >
    <div
      class="the-wallet-new-btc__balance"
      @click="handleMyWallet"
    >
      <div class="the-wallet-new-btc__balance-item">
        <div class="the-wallet-new-btc__balance-amount right contract">
          <div class="the-wallet-new-btc__balance-label-top">
            <e-tooltip position="left">
              <template slot="label">
                Contract
              </template>
              <template slot="message">
                The contract balance is the total balance on the contract, including the funds reserved in positions. This balance can be used for trading.
              </template>
            </e-tooltip>
          </div>

          <div class="the-wallet-new-btc__balance-value">
            <e-icon
              v-if="isContractBalanceNotReady"
              :src="$options.icons.iconDottedAnimation"
              class="the-wallet-new-btc__dotted-animation"
            />

            <template v-else>
              <e-big-number
                v-if="balance"
                :value="balance.contractBalance"
                :decimal-places="6"
              />
              <span class="the-wallet-new-btc__balance-token">BTC</span>
            </template>
          </div>
          <!--<div class="the-wallet-new-btc__balance-label" v-else-if="isETH">-->
            <!--≈ <e-big-number v-if="contractBalanceUsd" :value="contractBalanceUsd" :decimal-places="2"/> USD-->
          <!--</div>-->
        </div>

        <div class="the-wallet-new-btc__balance-amount right">
          <div class="the-wallet-new-btc__balance-label-top">
            <e-tooltip position="left">
              <template slot="label">
                Available
              </template>
              <template slot="message">
                The available balance is the total contract balance minus the amounts reserved in positions. The available balance can be used as collateral.
              </template>
            </e-tooltip>
          </div>
          <div class="the-wallet-new-btc__balance-value">
            <e-icon
              v-if="isContractBalanceNotReady || isAvailableBalanceRefreshing"
              :src="$options.icons.iconDottedAnimation"
              class="the-wallet-new-btc__dotted-animation"
            />

            <template v-else>
              <e-big-number
                v-if="balance"
                :value="balance.availableBalance"
                :decimal-places="6"
              />
              <span class="the-wallet-new-btc__balance-token">BTC</span>
            </template>
          </div>
          <!--<div class="the-wallet-new-btc__balance-label" v-if="isETH && !isAvailableBalanceRefreshing">-->
            <!--≈ <e-big-number v-if="availableBalanceUsd" :value="availableBalanceUsd" :decimal-places="2"/> USD-->
          <!--</div>-->
        </div>
      </div>

      <div class="the-wallet-new-btc__switcher">
        <div class="the-wallet-new-btc__switcher-wrapper" @click="stopPropagation">
          <div class="the-wallet-new-btc__switcher-button left" @click="startDeposit">
            <e-icon :src="$options.icons.iconArrow" class="the-wallet-new-btc__switcher-icon left" />
            <div class="the-wallet-new-btc__switcher-tooltip left">
              Deposit
            </div>
          </div>
          <div class="the-wallet-new-btc__switcher-button right" @click="startWithdrawal">
            <e-icon :src="$options.icons.iconArrow" class="the-wallet-new-btc__switcher-icon right" />
            <div class="the-wallet-new-btc__switcher-tooltip left">Withdraw</div>
          </div>
        </div>
        <div class="the-wallet-new-btc__switcher-loading" @click="stopPropagation">
          <e-icon
            :src="$options.icons.iconLoading"
            class="the-wallet-new-btc__switcher-icon-loading"
            v-if="anyBalanceNotReady"
          />
          <span
            v-else
            @click="refreshBalance"
            class="the-wallet-new-btc__switcher-title"
          >REFRESH</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * Компонент кошелька пользователя, отображается в верхнем меню всех страниц
 *
 * @version 1.0.3
 */

import { mapGetters } from 'vuex';
import balanceMixin from '@/util/mixins/balanceMixin';
import log from '@/util/logger';
import openLink from '@/util/openLink';
import { ASYNC_STATES, DEPOSIT_OPERATION, ETH_BASE_ADDRESS, BASE_TOKEN_BTC } from '@/util/constants';
import { routesMixin } from '@/util/mixins';

// Components
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';
import EBalanceTransferOperations from '@/components/EBalanceTransferOperations';
import ETooltip from '@/components/ETooltip';

// Icons
import iconWallet from '@/icons/e-icon__wallet.svg';
import iconArrow from '@/icons/e-icon_arrow_right.svg';
import iconDottedAnimation from '@/icons/e-icon_dotted-animation.svg';
import iconLoading from '@/icons/e-icon__loading.svg';

export default {
  name: 'the-wallet-new-btc',

  data() {
    return {
      isPrivateBalanceRefreshing: false,
      isContractBalanceRefreshing: false,
      isAvailableBalanceRefreshing: false,
      firstBalanceUpdate: false,
    };
  },

  computed: {
    isETH() {
      return this.baseToken === ETH_BASE_ADDRESS;
    },

    decimalBalance() {
      return this.isETH ? 4 : 2;
    },

    /**
     * Token balance
     * @returns {{privateBalance: BigNumber, contractBalance: BigNumber, inOrders: BigNumber, sum: BigNumber}}
     */
    balance() {
      return this.$store.getters['balance/getUserBalanceByToken'](BASE_TOKEN_BTC);
    },

    anyBalanceNotReady() {
      return this.isPrivateBalanceNotReady || this.isContractBalanceNotReady;
    },

    isPrivateBalanceNotReady() {
      return [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.$store.state.balance.ethInitStatus) || this.isPrivateBalanceRefreshing;
    },

    isContractBalanceNotReady() {
      return [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.$store.state.balance.ethInitStatus) || this.isContractBalanceRefreshing;
    },

    depositOperations() {
      return this.balance.operations.filter(o => o.type === DEPOSIT_OPERATION);
    },

    nonDepositOperations() {
      return this.balance.operations.filter(o => o.type !== DEPOSIT_OPERATION);
    },

    ...mapGetters({
      isLogin: 'wallets/isLogin',
      demoStep: 'ui/demoStep',
      activeWalletType: 'wallets/activeWalletType',
      privateBalanceName: 'wallets/walletBalanceName',
      theme: 'ui/getTheme',
      baseToken: 'balance/baseToken',
    }),
  },

  icons: {
    iconWallet,
    iconArrow,
    iconDottedAnimation,
    iconLoading,
  },

  watch: {
    baseToken() {
      if (this.isLogin) {
        this.refreshAvailableBalance();
      }
    },

    demoStep() {
      if (this.demoStep === 3) {
        this.refreshBalance();
      }
    },

    balance() {
      if (!this.firstBalanceUpdate) {
        this.refreshBalance();
      }

      this.firstBalanceUpdate = true;
    },
  },

  created() {
    // this.$store.dispatch('balance/listenToPrivateBalanceChanges');
    this.startListeningToEthBalance();
  },

  beforeDestroy() {
    // this.$store.dispatch('balance/stopListeningToPrivateBalanceChanges');
    this.stopListeningToEthBalance();
  },

  methods: {
    refreshBalance() {
      if (this.isLogin) {
        this.refreshPrivateBalance();
        this.refreshContractBalance();
        this.refreshAvailableBalance();
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    async refreshAvailableBalance() {
      const balanceApi = this.$store.getters['blockchain/balanceApi'];
      this.isAvailableBalanceRefreshing = true;

      let availableBalance;
      // const baseToken = this.$store.getters['futuresActiveContract/contractBaseToken'];

      try {
        availableBalance = await balanceApi.getAvailableBalance({ tokenAddress: this.baseToken });
      } catch (e) {
        log.warn(`Error @ Refresh ETH private balance button: ${e.message}`);
      }

      if (availableBalance) {
        this.$store.commit('balance/UPDATE_AVAILABLE_BALANCE', { balance: { availableBalance } });
      }
      this.isAvailableBalanceRefreshing = false;
    },

    async refreshPrivateBalance() {
      const balanceApi = this.$store.getters['blockchain/balanceApi'];
      this.isPrivateBalanceRefreshing = true;

      let balance;
      try {
        balance = await balanceApi.getPrivateEth();
      } catch (e) {
        log.warn(`Error @ Refresh ETH private balance button: ${e.message}`);
      }

      if (balance) {
        this.$store.commit('balance/UPDATE_PRIVATE_BALANCE', { eth: balance });
      }
      this.isPrivateBalanceRefreshing = false;
    },

    async refreshContractBalance() {
      const balanceApi = this.$store.getters['blockchain/balanceApi'];
      this.isContractBalanceRefreshing = true;

      let balance;
      try {
        balance = await balanceApi.getTradableEth();
      } catch (e) {
        log.warn(`Error @ Refresh ETH contract balance Button: ${e.message}`);
      }

      if (balance) {
        this.$store.commit('balance/UPDATE_TRADABLE_BALANCE', { balance: { contractBalance: balance } });
      }
      this.isContractBalanceRefreshing = false;
    },

    openLink,

    startDeposit() {
      if (this.isLogin) {
        this.$store.dispatch('deposit/startDeposit', this.baseToken);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    startWithdrawal() {
      if (this.isLogin) {
        this.$store.dispatch('deposit/startWithdrawal', this.baseToken);
      } else {
        this.$router.push({ name: 'signup' });
      }
    },

    handleMyWallet() {
      if (!this.isLogin) {
        this.$router.push({ name: 'signup' });
      }
    },

    stopPropagation(e) {
      e.stopPropagation();
    },
  },

  components: {
    EBigNumber,
    EIcon,
    EButton,
    EBalanceTransferOperations,
    ETooltip,
  },

  mixins: [routesMixin, balanceMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-new-btc {
  $this: &;

  width: 100%;
  height: 100%;
  position: relative;
  max-width: 240px;
  margin-left: auto;

  @include r(1279) {
    margin-left: 0;
  }

  &__balance {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 11px;
    box-sizing: border-box;

    @include r(1279) {
      padding: 0 10px;
    }
  }

  &__balance-item {
    min-width: 105px;
  }

  &__balance-icon {
    width: 29px;
    height: 26px;

    @include r(600) {
      display: none;
    }

    &.left {
      margin-right: 11px;

      .e-balance-transfer-operations__operation {
        justify-content: flex-start;
      }
    }

    &.right {
      margin-left: 10px;

      .e-balance-transfer-operations__operation {
        justify-content: flex-end;
      }
    }

    .e-icon__wallet-colored {
      fill: #275379;
      transition: fill .15s ease-in-out;
    }

    &.metamask {
      .e-icon__wallet-colored {
        fill: #F4852D;
      }
    }
  }

  &__balance-amount {
    min-width: 80px;
    line-height: 10px;
    position: relative;
    display: flex;
    justify-content: space-between;

    &.right {
      text-align: right;

      #{$this}__balance-value {
        justify-content: flex-end;
      }
    }

    &.contract {
      &:after {
        /*content: '/';*/
        position: absolute;
        right: -12px;
        top: 0;
        color: rgba(92,96,98,0.30);
      }
    }
  }

  &__balance-value {
    display: flex;
    color: #0D105C;
    font-size: 11px;
    height: 13px;
    margin-bottom: 0;
    letter-spacing: .25px;
    font-weight: 500;
    transition: color .15s ease-in-out;
    margin-left: 8px;

    .e-big-number {
      padding-right: 4px;
    }
  }

  &__balance-token {
    transition: color .15s ease-in-out;
  }

  &__balance-label-top {
    color: $color-light-grey;
    font-size: 8px;
    text-transform: uppercase;
    height: 13px;
    margin-bottom: 2px;
    font-weight: 500;
    margin-right: 4px;

    .e-tooltip {
      justify-content: flex-end;
    }
  }

  &__balance-label {
    color: #9ea1bd;
    opacity: 1;
    font-weight: 600;
    font-size: 9px;
    height: 13px;
    text-transform: uppercase;
    transition: all .15s ease-in-out;

    .e-tooltip {
      justify-content: flex-end;
    }
  }

  &__switcher {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include r(1279) {
      margin-left: 88px;
    }

    @include r(640) {
      margin-left: 0;
    }
  }

  &__switcher-wrapper {
    display: flex;
    margin: 0 11px;
  }

  &__switcher-button {
    width: 22px;
    height: 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    background: #0097FE;
    transition: all .15s ease-in-out;
    position: relative;
    cursor: pointer;

    &.left {
      border-radius: 4px 0 0 4px;
      border-right: 1px solid #0097FE;

      &:after {
        content: '';
        display: block;
        width: 1px;
        height: 13px;
        position: absolute;
        z-index: 1;
        right: -1px;
        top: 3px;
        background: #fff;
      }

      &:hover,
      &:active {
        &.left:after {
          display: none;
        }
      }
    }

    &.right {
      border-radius: 0 4px 4px 0;
      border-left: 1px solid #0097FE;
      margin-left: -1px;
    }

    #{$this}__switcher-icon {
      svg {
        fill: #fff;
      }
    }

    &:hover {
      z-index: 1;
      background: #1F2EE9;
      border-color: #1F2EE9;

      #{$this}__switcher-icon {
        svg {
          fill: #fff;
        }
      }

      #{$this}__switcher-tooltip {
        opacity: 1;
        top: -2px;

        &.left {
          transform: translate(calc(-100% - 9px), 0);
        }
        &.right {
          transform: translate(calc(100% + 9px), 0);
        }
      }
    }

    &:active {
      background: #161E8A;
      border-color: #161E8A;

      #{$this}__switcher-icon {
        top: calc(50% + 1px);

        svg {
          fill: #fff;
        }
      }
    }
  }

  &__switcher-loading {
    cursor: pointer;
    font-size: 8px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    color: #1F2EE9;
    padding-top: 4px;

    .e-icon {
      margin-right: 3px;
    }

    &:hover {
      #{$this}__switcher-title {
        opacity: .6;
      }
    }
  }

  &__switcher-title {
    transition: all .15s ease-in-out;
  }

  &__switcher-icon-loading {
    width: 12px;
    height: 12px;
  }

  &__switcher-icon {
    position: absolute;
    left: 50%;
    top: 50%;

    &.left {
      transform: translate(-50%, -50%) rotate(90deg);
      width: 14px;
      height: 14px;
    }

    &.right {
      transform: translate(calc(-50% - 1px), -50%) rotate(-90deg);
      width: 14px;
      height: 14px;
    }

    svg {
      fill: #1F2EE9;
      transition: fill .2s ease-in-out;
    }
  }

  &__switcher-tooltip {
    display: flex;
    align-items: center;
    padding: 0 8px;
    box-sizing: border-box;
    height: 22px;
    opacity: 0;
    top: -9999px;
    position: absolute;
    font-size: 10px;
    font-weight: 500;
    color: #fff;
    background: #2D2F72;
    white-space: nowrap;
    border-radius: 2px;
    transition: opacity .2s ease-in-out, transform .2s ease-in-out;

    &:before {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: calc(50% - 5px);
      border-top: 5px solid transparent;
      border-bottom: 5px solid transparent;
    }

    &.left {
      left: 0;
      transform: translate(calc(-100% - 5px), 0);

      &:before {
        border-left: 6px solid #2D2F72;
        right: -5px;
      }
    }

    &.right {
      right: 0;
      transform: translate(calc(100% + 5px), 0);

      &:before {
        border-right: 6px solid #2D2F72;
        left: -5px;
      }
    }
  }

  &__find-balance {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-sizing: border-box;
    background: #F9F9FB;
  }

  &__find-balance-content {
    line-height: 16px;
  }

  &__find-balance-headline {
    color: #0D105C;
    font-size: 14px;
    font-weight: 500;
  }

  &__find-balance-description {
    font-size: 12px;
  }

  &__dotted-animation {
    width: 48px;
    transform: translateY(30%);
    height: 14px;
    margin-left: 2px;
  }

  &__pending {
    position: relative;

    .e-balance-transfer-operations__amount {
      display: flex;
    }

    .e-balance-transfer-operations__link .e-icon {
      top: -1px;
      margin-right: 3px;
      width: 10px;
      height: 10px;
    }

    .e-balance-transfer-operations__link {
      margin: 0 0 0 4px;
    }

    .e-balance-transfer-operations__info {
      padding-right: 0 !important;
    }
  }

  &__dark {
    #{$this}__balance-value,
    #{$this}__balance-token {
      color: #fff;
    }

    .e-balance-transfer-operations__link-txt {
      color: $color-blue-dark-mode;
    }

    .e-balance-transfer-operations__link {
      svg path {
        fill: $color-blue-dark-mode;
      }
    }

    #{$this}__balance-label,
    #{$this}__switcher-title {
      color: #fff;
      opacity: 1;

      &:hover {
        opacity: 1;
      }
    }

    #{$this}__balance-label-top {
      color: #D9D9D9;
      opacity: 1;

      &:hover {
        opacity: 1;
      }
    }

    #{$this}__switcher-tooltip {
      background: $color-blue-dark-mode;

      &.left {
        &::before {
          border-left-color: $color-blue-dark-mode;
        }
      }

      &.right {
        &::before {
          border-right-color: $color-blue-dark-mode;
        }
      }
    }

    #{$this}__switcher-title:hover {
      opacity: .9;
    }

    #{$this}__balance-icon {
      .e-icon__wallet-colored {
        fill: #343E4E;
      }

      &.metamask {
        .e-icon__wallet-colored {
          fill: #F4852D;
        }
      }
    }

    #{$this}__switcher-button {
      border-color: #434b5a;
      background: #434b5a;

      &.left {
        &:after {
          background: #000000;
        }
      }

      &:hover {
        background: $color-blue-dark-mode;
        border-color: $color-blue-dark-mode;

        &.left {
          &:after {
            display: none;
          }
        }
      }
    }

    #{$this}__switcher-icon svg {
      fill: #fff;
    }

    #{$this}__dotted-animation svg g > g {
      fill: #fff;
    }

    #{$this}__switcher-icon-loading svg circle {
      fill: #fff;
    }
  }
}
</style>



