import get from 'lodash/get';
import backend from './endpoint';

// eslint-disable-next-line import/prefer-default-export
export const getStats = async () => backend.httpProd.get('/api/futures/stats');

export const setTourStep = async ({ walletAddress, step }) =>
  backend.httpProd.post('/api/futures/tour-steps', {
    wallet_address: walletAddress,
    step: `${step}`,
    device_type: get(navigator, 'userAgent'),
    lang: get(navigator, 'language'),
    screen_size: `${window.innerWidth}:${window.innerHeight}`,
  });



