<script>
import debounce from 'lodash/debounce';

import 'swiper/dist/css/swiper.css';
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import { mapGetters } from 'vuex';
import canUseWebP from '@/util/canUseWebP';

import EIcon from '@/components/EIcon';

import iconWorkArrow from '@/icons/e-icon__work-arrow.svg';
import iconMedium from '@/icons/e-icon__medium.svg';

const SWIPER_OPTIONS = {
  slidesPerView: 'auto',
  centeredSlides: true,
  spaceBetween: 20,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
};

export default {
  name: 'the-latest-blog-posts',

  data() {
    return {
      // Icons
      iconWorkArrow,
      iconMedium,
      canUseWebP,

      adaptiveVersionFeatures: false,
    };
  },

  computed: {
    ...mapGetters({
      windowSize: 'ui/windowSize',
    }),
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
    this.SWIPER_OPTIONS = SWIPER_OPTIONS;
  },

  mounted() {
    this.onResize();
  },

  methods: {
    onResize() {
      this.adaptiveVersionFeatures = this.windowSize.width < 992;
    },
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },
  },

  components: {
    swiper,
    swiperSlide,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-latest-blog-posts {
