    text-decoration: none;
    flex: 0 0 calc(33.33% - 20px);
    margin: 0 10px;
    background: #ECECEF;
    border-radius: 30px;
    padding: 36px 0 0;
    border: 2px solid #ECECEF;
    box-sizing: border-box;
    transition: transform .15s ease-in-out;

    &:hover {
      transform: translate(0, -5px);

      #{$this}__list-item-content {
        background: #ECECEF;
      }
    }

    @include r(992) {
      margin: 0;
    }
  }

  &__list-item-image {
    border-radius: 36px;
    margin: 0 6px;
    overflow: hidden;
    position: relative;
    line-height: 0;
    box-shadow: 0 8px 18px 0 rgba(6,10,56,0.10);

    z-index: 2;

    img {
      width: 100%;
    }
  }

  &__list-item-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    position: relative;
    z-index: 1;
    background: #fff;
    border-radius: 28px;
    padding: 150px 30px 30px;
    margin-top: -120px;
    box-sizing: border-box;
    transition: background-color .15s ease-in-out;
  }

  &__list-item-title {
    font-weight: 500;
    font-size: 23px;
    color: #112271;
    letter-spacing: 0;
    line-height: 28px;
    margin-bottom: 10px;
    max-height: 56px;
    overflow: hidden;

    @include r(1279) {
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__list-item-text {
    font-weight: 500;
    font-size: 14px;
    color: #6B7397;
    letter-spacing: -.25px;
    line-height: 22px;
    min-height: 88px;
    overflow: hidden;
    margin-bottom: 18px;
  }

  &__list-item-data {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
  }

  &__list-item-readmore {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    color: #112271;
    letter-spacing: 0;

    @include r(640) {
      font-size: 14px;
    }

    .e-icon {
      width: 10px;
      height: 12px;
      margin-top: -2px;
      margin-right: 8px;

      svg path {
        fill: #00DB9C;
      }
    }
  }

  &__list-item-date {
    font-size: 13px;
    color: #6B7397;
    letter-spacing: 0;
    line-height: 24px;
  }

  &__readmore {
    margin-top: 45px;
    display: flex;
    justify-content: center;
  }

  &__readmore-link {
    width: 100%;
    max-width: 290px;
    height: 50px;
    border: 3px solid #00DB9C;
    box-shadow: 0 12px 18px 0 rgba(17,34,113,0.05);
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 18px;
    color: #112271;
    letter-spacing: 0;
    text-align: center;
    line-height: 26px;
    border-radius: 12px;
    text-decoration: none;
    transition: all .15s ease-in-out;

    &:hover {
      background: #00e0a0;
      border-color: #00e0a0;
      color: #fff;
    }

    &:active {
      transform: translate(0, 1px);
    }

    .e-icon {
      width: 26px;
      height: 26px;
      margin-right: 12px;
    }
  }

  &__slider {
    position: relative;
    margin-top: 30px;

    .swiper-container {
      overflow: visible;
    }

    .swiper-slide {
      width: 50%;
      padding-bottom: 30px;

      @include r(599) {
        width: 80%;
        padding-bottom: 10px;
      }
    }

    .swiper-pagination {
      bottom: 9px;
      position: relative;
      z-index: 3;
    }

    .swiper-pagination-bullet {
      width: 5px;
      height: 5px;
      margin: 0 7px;
      transition: .3s ease;
      background-color: #040B48;
      outline: none;
    }

    .swiper-pagination-bullet-active {
      transform: scale(1.3);
    }
  }
}
</style>



