<script>
import { mapGetters, mapMutations, mapState } from 'vuex';
import { APP_ENV } from '@/config';
import { BASE_TOKEN_BTC } from '@/util/constants';

import { getFeeCredit } from '@/api/backend/wallet';
import { setStorageValue } from '@/util/storage';
import log from '@/util/logger';
import { getAssets } from '@/api/backend/futures/assets';
import { setTourStep } from '@/api/backend/statistics';

import iconMenu from '@/icons/e-icon_menu.svg';
import iconMoon from '@/icons/e-icon__moon.svg';
import iconDay from '@/icons/e-icon__day.svg';
import iconGithubRaw from '@/icons/e-icon__github-raw.svg';
import iconTwitterRaw from '@/icons/e-icon__twitter-raw.svg';
import iconTelegramRaw from '@/icons/e-icon__telegram-raw.svg';
import iconSupport from '@/icons/e-icon__support.svg';
import iconFile from '@/icons/e-icon__file.svg';
import iconTour from '@/icons/e-icon__tour.svg';
import iconArrowDropdown from '@/icons/e-icon__arrow-dropdown.svg';

import TheLogoInner from '@/containers/blocks/TheLogoInner';
import TheWalletNewBtc from '@/containers/blocks/TheWalletNewBtc';
import TheWalletNew from '@/containers/blocks/TheWalletNew';
import TheHeaderFuturesContracts from '@/containers/blocks/TheHeaderFuturesContracts';
import EIcon from '@/components/EIcon';
import EDemo from '@/components/EDemo';
import EButton from '@/components/EButton';
import EWalletList from '@/components/EWalletList';
import ETooltip from '@/components/ETooltip';

import { routesMixin } from '@/util/mixins';

// TODO: fix styles
export default {
  name: 'the-header-inner',

  icons: {
    iconMenu,
    iconMoon,
    iconDay,
    iconGithubRaw,
    iconTwitterRaw,
    iconTelegramRaw,
    iconSupport,
    iconFile,
    iconTour,
    iconArrowDropdown,
  },

  props: {
    headerFromIndex: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      setStorageValue,
      isMenuOpen: false,
      feeCredit: undefined,
      isContractOpen: false,
      // contractSelected: { key: '7.0', value: '7.0' },
      // contractList: [{ key: '7.0', value: '7.0' }, { key: '6.0', value: '6.0' }, { key: '5.0', value: '5.0' }],
    };
  },

  computed: {
    selectedContract() {
      return this.exchangeContracts && this.exchangeContracts.length > 0 && this.exchangeContractId
        ? this.exchangeContracts.find(item => item.id === this.exchangeContractId)
        : null;
    },

    exchangeContracts() {
      return this.$store.getters['contracts/exchangeContracts'].map(item => ({
        ...item,
      }));
    },

    /**
     * Текущая выбранная версия главного контракта
     */
    exchangeContractId: {
      get() {
        return this.$store.getters['contracts/activeMainContract'].id;
      },
      async set(newId) {
        const contract = this.exchangeContracts.find(item => item.id === newId);
        if (contract) {
          await this.$store.dispatch('contracts/changeActiveMainContract', contract);
        }
      },
    },

    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
      demoStep: 'ui/demoStep',
      walletAddress: 'wallets/activeWalletAddress',
      isLogin: 'wallets/isLogin',
      theme: 'ui/getTheme',
      baseToken: 'balance/baseToken',
      btcBaseToken: 'balance/btcBaseToken',
    }),

    ...mapState({
      activeFuturesSmartContract: state => state.contracts.activeFuturesContract,
    }),
  },

  watch: {
    walletAddress() {
      this.getFeeCredit();
    },

    baseToken() {
      this.handleBaseToken();
    },
  },

  created() {
    this.$store.dispatch('wallets/loadPrivateWallets');
  },

  async mounted() {
    this.getFeeCredit();
    this.$store.commit('futuresActiveContract/SET_ACTIVE_CONTRACT_BASE_TOKEN', this.baseToken);
  },

  methods: {
    openMenu() {
      this.isMenuOpen = true;
    },

    toggleMenu() {
      setTimeout(() => {
        this.isMenuOpen = !this.isMenuOpen;
      }, 0);
    },

    closeMenu() {
      this.isMenuOpen = false;
    },

    openContract() {
      this.isContractOpen = true;
    },

    toggleContract() {
      setTimeout(() => {
        this.isContractOpen = !this.isContractOpen;
      }, 0);
    },

    closeContract() {
      this.isContractOpen = false;
    },

    async handleBaseToken() {
      const result = await getAssets({ contractAddress: this.activeFuturesSmartContract.contractAddress, baseToken: this.baseToken });

      this.$store.commit('futuresPositions/SET_POSITION_TO_CLOSE', null);
      this.$store.commit('futuresPositions/SET_PREVIOUS_CONTRACT_HASH', null);

      setTimeout(() => {
        this.$router.push({ name: 'future', params: { contract: result[0].recommendedContract.futuresContractHash } });
      }, 200);
    },

    handleSignUp() {
      this.$router.push({ name: 'signup' });
    },

    handleGenerate() {
      this.setLoginFlow(5);
    },

    toggleTheme() {
      if (this.theme === 'light') {
        this.$store.commit('ui/SET_THEME_DARK');
      } else {
        this.$store.commit('ui/SET_THEME_LIGHT');
      }
    },

    startTour() {
      setStorageValue('APP_ENV', 'test');
      setTourStep({ walletAddress: this.activeWallet.address, step: 0 });
      this.$store.commit('ui/SET_DEMO_STEP', 1);

      if (APP_ENV !== 'test') {
        window.location.href = '/f';
      }
    },

    async getFeeCredit() {
      try {
        const { data: { data: { fee_credit: feeCredit } } } = await getFeeCredit({ walletAddress: this.walletAddress });
        this.feeCredit = feeCredit;
      } catch (e) {
        log.error(e);
      }
    },

    selectContract(contractId, isActive) {
      if (!isActive) {
        this.exchangeContractId = contractId;
        if (this.$refs.walletNew) {
          this.$refs.walletNew.refreshBalance();
        }
      }
    },

    setBaseToken(baseToken) {
      if (!this.isLogin) {
        this.showLoginFlow();
        return;
      }

      if (baseToken === 'BASE_TOKEN_BTC') {
        // this.$store.commit('tokens/SET_ACTIVE_TOKEN_SUCCESS', ACTIVE_TOKEN_DAI);
        this.$store.dispatch('tokens/setActiveToken', 'BTC');
        this.setStorageValue('BASE_TOKEN_BTC', baseToken);

        this.$store.commit('balance/SET_BASE_BTC_TOKEN', baseToken);
        this.$store.commit('balance/SET_BASE_TOKEN', BASE_TOKEN_BTC);
        this.$store.commit('futuresActiveContract/SET_ACTIVE_CONTRACT_BASE_TOKEN', BASE_TOKEN_BTC);
        this.setStorageValue('BASE_TOKEN', BASE_TOKEN_BTC);

        setTimeout(() => {
          this.$refs.walletNewBtc.refreshBalance();
        }, 100);
      } else {
        this.$store.commit('balance/SET_BASE_BTC_TOKEN', null);
        this.setStorageValue('BASE_TOKEN_BTC', null);
        this.$store.dispatch('tokens/setActiveToken');

        this.$store.commit('balance/SET_BASE_TOKEN', baseToken);
        this.$store.commit('futuresActiveContract/SET_ACTIVE_CONTRACT_BASE_TOKEN', baseToken);
        this.setStorageValue('BASE_TOKEN', baseToken);

        setTimeout(() => {
          this.$refs.walletNew.refreshBalance();
        }, 100);
      }
    },

    ...mapMutations({ showLoginFlow: 'ui/SHOW_LOGIN_FLOW', setLoginFlow: 'ui/SET_LOGIN_FLOW' }),
  },

  components: {
    EIcon,
    EDemo,
    EButton,
    EWalletList,
    TheLogoInner,
    TheWalletNewBtc,
    TheWalletNew,
    TheHeaderFuturesContracts,
    ETooltip,
  },

  mixins: [routesMixin],
};
</script>

