    margin-right: 6px;
  }

  &__select-contract-wrapper {
    position: relative;
    width: 50px;
    font-size: 11px;
    font-weight: 500;
    color: $color-blue;
    cursor: pointer;

    &.isOpen {
      #{$this}__select-contract-options {
        opacity: 1;
        left: 0;
        transform: translate(0, 100%);
      }
    }
  }

  &__select-contract-result {
    width: 100%;
    height: 26px;
    background: $color-blue-dark-mode;
    border-radius: 4px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;

    .e-icon {
      width: 9px;
      height: 6px;

      svg {
        width: 9px;
        height: 6px;

        path {
          fill: #fff;
        }
      }
    }
  }

  &__select-contract-options {
    opacity: 0;
    position: absolute;
    width: 100%;
    left: -9999px;
    bottom: -1px;
    background: #eee;
    border-radius: 4px;
    overflow: hidden;
    transform: translate(0, calc(100% - 1px));
    transition: transform .15s ease-in-out;
  }

  &__select-contract-option-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 24px;
    padding: 0 10px;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(0,0,0,.05);
    transition: background-color .15s ease-in-out, color .15s ease-in-out;

    &:hover {
      background: $color-blue-dark-mode;
      color: #fff;
    }

    &:last-child {
      border-bottom: none;
    }

    &.active {
      cursor: default;
      color: $color-dark-grey;

      &:hover {
        background: none;
      }
    }
  }

  &__select-contract-option-current {
    display: block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $color-red;

    &.current {
      background-color: $color-green;
    }
  }

  &__demo-overlay {
    content: '';
    position: fixed;
    z-index: 28;
    top: 0;
    left: 0;
    display: block;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,.6);
  }

  &__buttons-switcher {
    position: relative;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    width: 90px;
    height: 26px;
    padding: 2px;
    box-sizing: border-box;
    background: rgba(92, 96, 98, 0.1);
    border-radius: 4px;
    margin-left: 5px;
    transition: background-color .15s ease-in-out;

    @include r(1439) {
      margin-left: 0;
    }

    &.margin-currency {
      position: relative;
      width: 135px;

      &:before {
        content: '';
        display: block;
        position: absolute;
        z-index: 0;
        width: 41px;
        height: 22px;
        top: 2px;
        left: 2px;
        background: #0097FE;
        border-radius: 4px;
        transform: translate(0, 0);
        transition: transform .15s ease-in-out, background-color .15s ease-in-out;
      }

      &.eth:before {
        background: #0097FE;
      }

      &.dai:before {
        background: #F7B14A;
        color: $color-black !important;
        transform: translate(90px, 0);
      }

      &.btc:before {
        background: #F7B14A;
        color: $color-black !important;
        transform: translate(45px, 0);
      }

      #{$this}__buttons-switcher-btn {
        cursor: pointer;
      }
    }
  }

  &__buttons-switcher-btn,
  &__buttons-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    width: 41px;
    height: 22px;
    color: #9ea1bd;
    font-size: 10px;
    font-weight: 500;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    text-decoration: none;
    transition: background-color .15s ease-in-out, color .15s ease-in-out;

    &:hover {
      background: rgba(0,0,0,.05);
    }

    &.test {
      color: $color-black !important;
      background: $color-green-dark;

      &:hover {
        background: $color-green-dark !important;
      }
    }

    &.active {
      color: #fff;
      background: #DC4727;

      &:hover {
        background: #DC4727 !important;
      }
    }

    &.active-btn {
      color: #fff;
    }

    &.active-btn-dai {
      color: $color-black !important;
    }
  }

  &__buttons-btn {
    height: 24px;
    margin-right: 4px;
    background: #0097FE;
    color: #fff;
  }

  &__buttons-support-setting {
    font-size: 8px;
    position: absolute;
    z-index: 1;
    margin: 9px 0 0 -9px;
    overflow: hidden;
    width: 11px;
    height: 6px;
    transform: rotate(70deg);
  }

  &__fee-credit {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 0 0 180px;
    color: #5C6062;
    background: #fff;
    border-left: 1px solid #eeefef;
    transition: background-color .15s ease-in-out;

    @include r(1439) {
      flex: 0 0 120px;
    }

    @include r(1279) {
      order: 3;
      flex: 1;
    }
  }

  &__fee-credit-wrapper {
    display: flex;
    width: 100%;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;

    @include r(1439) {
      padding: 0 10px;
    }
  }

  &__fee-credit-label {
    font-weight: 500;
    font-size: 8px;
    line-height: 10px;
    color: $color-light-grey;
    text-transform: uppercase;
    margin-right: 10px;
  }

  &__fee-credit-value-wrapper {
    display: flex;
    align-items: center;
    background: rgba(92, 96, 98, 0.1);
    border-radius: 4px;
    width: 86px;
    height: 26px;
    box-sizing: border-box;
    padding: 0 9px;

    @include r(1439) {
      width: 75px;
    }

    .e-tooltip {
      margin-left: auto;
      height: 10px;
    }

    .e-tooltip__label {
      width: 10px;
      height: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
      font-size: 8px;
      color: #5f5f5f;
      background: #e4e4e4;
      border-radius: 50%;
      margin-top: -2px;
    }
  }

  &__fee-credit-value {
    font-weight: 500;
    font-size: 11px;
    color: #0123E7;
    letter-spacing: 0.5px;
  }

  &__buttons-green,
  &__buttons-opacity {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    height: 26px;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 500;
    transition: all .15s ease-in-out;
    text-decoration: none;
    margin: 0 5px;
    color: #55555a;
    white-space: nowrap;

    &:active {
      transform: translate(0, 1px);
    }
  }

  &__buttons-green {
    background-color: rgba(0,219,156,.3);

    &:hover {
      background-color: rgba(0,219,156,.45);
    }

    .green {
      color: #11bd73;
    }
  }

  &__buttons-opacity {
    border: 2px solid rgba(243,134,45,0.40);

    &:hover {
      background: #ce2636;
      border-color: transparent;
      color: #fff;
    }
  }

  &__wallet {
    position: relative;
    flex: 1;
    border-left: 1px solid #eeefef;
    border-right: 1px solid #eeefef;
    width: 100%;
    min-width: 376px;
    background-color: #fff;
    z-index: 11;
    transition: background-color .15s ease-in-out;

    @include r(1279) {
      width: 100%;
      height: 45px;
      flex: 0 0 100%;
      min-width: 0;
      z-index: 13;
      order: 5;
      border-bottom: 1px solid #eeefef;
    }
  }

  &__fee-credit,
  &__action-item.toggleTheme,
  &__action-item.docs,
  &__action-item.support,
  &__action-item.startTour {
    @include r(1279) {
      border-bottom: 1px solid #eeefef;
    }
  }

  &__navigation-cover {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#02031B, .3);
    z-index: 999;
    opacity: 0;
    visibility: hidden;
  }

  &__toggler {
    align-items: center;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    padding: 18px 9px 17px;
    transition: background-color ease-in-out .15s, border-color ease-in-out .15s;
    justify-content: center;
    border-left: 1px solid #eeefef;

    &:hover {
      background-color: #F3F6FF;
    }

    @include r(1279) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__toggler-text {
    font-size: 9px;
    color: $color-blue;
    letter-spacing: 1px;
    font-weight: 500;
    text-transform: uppercase;
    transition: color .15s ease-in-out;

    @include r(1279) {
      display: none;
    }
  }

  &__toggler-icon {
    height: 26px;
    margin: 0 auto -3px;
    width: 26px;

    .e-icon svg {
      fill: $color-blue;
      transition: fill .15s ease-in-out;
    }
  }

  &__wallet-list {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    position: relative;
    z-index: 12;
    background-color: #fff;
    flex-basis: calc(20% - 15px);
    min-width: 306px;
    transition: background-color .15s ease-in-out;

    @include r(1279) {
      flex: 0 0 calc(100% - 112px);
      min-width: 0;
      max-width: none;
      order: 2;
      z-index: 14;
      border-left: 1px solid #eeefef;
      border-bottom: 1px solid #eeefef;
    }
  }

  &__demo-wallet {
    position: absolute;
    z-index: 38;
    left: 50%;
    top: 0;
    transform: translate(calc(-50% - 22px), 62px);


    @include r(1279) {
      left: 0;
      right: auto;
      transform: translate(6px, 54px);
    }
  }

  &__demo-wallet-list {
    position: absolute;
    z-index: 38;
    left: 0;
    top: 0;
    transform: translate(calc(-100% - 5px), 5px);


    @include r(1279) {
      left: 0;
      right: auto;
      transform: translate(-73px, 53px);
    }
  }

  &__wallet-list_smaller {
    width: 390px;
    min-width: auto;

    @include r(1200) {
      width: 344px;
    }
  }

  &__auth {
    align-items: center;
    display: flex;
    width: 100%;
    flex-direction: row;
    padding: 10px 15px;
    justify-content: center;
    transition: background-color .15s ease-in-out;

    button {
      padding-left: 10px !important;
      padding-right: 10px !important;
    }

    @include r(640) {
      padding: 5px;

      button {
        padding-left: 4px !important;
        padding-right: 4px !important;
      }
    }
  }

  &__textual {
    color: $color-grey;
    font-size: 10px;
    max-width: 162px;
    line-height: 1.4;
    letter-spacing: 0.67px;
    margin-right: 20px;
    text-align: center;
  }

  &__btn {
    min-width: 94px;

    .e-button_padding_inner {
      padding-top: 0;
    }

    @include r(1200) {
      min-width: 75px;
    }

    @include r(640) {
      .e-button_size_s {
        font-size: 9px;
      }
    }
  }

  &__text {
    margin: 0 8px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    transition: color .15s ease-in-out;
  }

  &__search {
    background: #fff;
    min-width: 230px;
    border-left: 1px solid #eeefef;
    border-bottom: 2px solid #a5abf6;
    flex: 1;
    z-index: 13;
    margin-left: -1px;
    transition: all .15s linear;

    .the-token-search-result-new {
      height: auto;
      max-height: 255px;
      box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
      border-radius: 0 0 5px 5px;
    }

    &.is-opened {
      background: #F2F6FF;
      border-bottom-color: transparent;
    }

    @include r(1279) {
      z-index: 14;
      flex: 0 0 100%;
      order: 4;
      height: 45px;
      border-bottom-width: 1px;
    }
  }

  &__search-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
  }

  &__search-input {
    width: 100%;
    height: 100%;
    box-sizing: border-box !important;
    padding: 0 32px 4px 40px;
    font-size: 14px;
    font-weight: 500;
    color: #0D105C;
    border-radius: 0;
    text-transform: uppercase;
    -webkit-appearance: none;
    caret-color: #1F2EE9;
    transition: all .15s ease-in-out;

    &::placeholder {
      text-transform: none;
      color: #0D105C;
      transition: color .15s ease-in-out;
      letter-spacing: -.4px;
    }

    &:hover {
      &::placeholder {
        color: #1F2EE9;
      }

      + #{$this}__search-icon {
        svg {
          fill: #1F2EE9;
        }

        + #{$this}__search-selected {
          #{$this}__search-selected-name {
            color: #1F2EE9;
          }
        }
      }
    }

    &:focus {
      &::placeholder {
        color: rgba(129,168,247,0.50);
      }

      + #{$this}__search-icon {
        svg {
          fill: #1F2EE9;
        }
      }
    }
  }

  &__search-close {
    position: absolute;
    z-index: 1;
    right: 15px;
    top: calc(50% - 8px);
    transition: .3s ease;

    @include r(1279) {
      right: 12px;
    }

    @include r(640) {
      right: 8px;
    }

    .e-icon {
      width: 12px;
      height: 12px;
      cursor: pointer;

      svg {
        transition: all .15s ease-in-out;
        fill: #0123E7;
      }
    }
  }

  &__search-close-active {
    transform: rotate(180deg);
  }

  &__search-selected-info-wrapper {
    margin-left: auto;
    display: flex;
    align-items: center;
    text-transform: uppercase;

    @include r(1279) {
      margin-left: 30px;
    }
  }

  &__search-selected {
    display: flex;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 15px;
    transform: translate(0, -50%);
    width: calc(100% - 15px);
    padding-right: 40px;
    box-sizing: border-box;
    cursor: pointer;
    font-size: 9px;
    line-height: 12px;

    @include r(1279) {
      left: 10px;
      width: calc(100% - 20px);
    }
  }

  &__search-selected-name-wrapper {
    display: flex;
    align-items: center;
  }

  &__search-selected-name-icon {
    flex: 0 0 28px;
    width: 28px;
    height: 28px;
    margin-right: 10px;
  }

  &__search-selected-info-date-wrapper,
  &__search-selected-info-leverage-wrapper,
  &__search-selected-info-price-wrapper {
    margin-left: 18px;
    text-align: right;
    min-width: 70px;
  }

  &__search-selected-info-value {
    height: 14px;
    font-weight: 500;
    text-align: left;

    &.-price {
      font-weight: 600;
      font-size: 10px;
      color: $color-dark-blue;
    }
  }

  &__search-selected-info-leverage-value {
    display: flex;
    justify-content: flex-end;
  }

  &__search-selected-info-leverage-value-item {
    text-transform: uppercase;
    font-weight: 600;
    width: 25px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    margin-left: 2px;

    &.-increase {
      color: #1FCE43;
      background: rgba(31,206,67,0.10);
    }

    &.-decrease {
      color: #DA4830;
      background: rgba(220,71,39,0.10);
    }
  }

  &__search-selected-info-label {
    display: flex;
    align-items: center;
    color: #9ea1bd;
    opacity: 1;
    font-weight: 600;
    height: 13px;
    text-transform: capitalize;
    margin-bottom: 5px;
  }

  &__search-selected-token {
    display: flex;
    font-size: 10px;
    font-weight: 500;
  }

  &__search-selected-name {
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    font-weight: 500;
    height: 14px;
    padding-bottom: 0;
    color: #0D105C;
    transition: color .15s ease-in-out;
  }

  &__search-selected-token-code {
    font-weight: 600;
    width: 70px;
    color: #0D105C;
    opacity: .4;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    transition: color .15s ease-in-out;
  }

  &__search-selected-etherscan {
    display: flex;
    align-items: center;
    text-decoration: none;
    margin-left: 4px;
    color: #0D105C;
    transition: color .15s ease-in-out;

    .e-icon {
      width: 8px;
      height: 8px;
      margin-left: 4px;

      svg #e-icon-link2-ui-kit {
        fill: #0D105C;
        transition: fill .15s ease-in-out;
      }
    }

    &:hover {
      color: #1F2EE9;

      .e-icon svg #e-icon-link2-ui-kit {
        fill: #1F2EE9;
      }
    }
  }

  &__action-item {
    position: relative;
    z-index: 1;
    flex-basis: 55px;
    min-height: 45px;
    flex-direction: column;
    min-width: 55px;
    background: #fff;
    font-size: 9px;
    font-weight: 500;
    text-transform: uppercase;
    color: #0123E7;
    text-decoration: none;
    border-left: 1px solid #eeefef;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color ease-in-out .15s, border-color ease-in-out .15s;

    &:hover {
      background-color: #F3F6FF;
    }

    &.isOpen {
      background-color: #F3F6FF;
    }

    .e-icon {
      width: 15px;
      height: 15px;
      margin-bottom: 2px;

      svg,
      svg path {
        fill: #0123E7;
      }
    }

    &.docs {
      .e-icon {
        width: 9px;
        height: 12px;
      }
    }

    @include r(1279) {
      &.support,
      &.toggleTheme,
      &.docs,
      &.startTour {
        order: 3;
      }
    }
  }

  &__action-item-submenu {
    position: absolute;
    bottom: 1000px;
    left: 0;
    transform: translate(0, calc(100% - 5px));
    background: #F3F6FF;
    width: 200px;
    padding: 20px 0 20px 30px;
    box-sizing: border-box;
    box-shadow: 0 12px 18px 0 rgba(1,35,231,.1);
    transition: transform .2s ease-in-out;

    &.isOpen {
      bottom: 0;
      transform: translate(0, 100%);
    }
  }

  &__action-item-submenu-heading {
    font-size: 9px;
    text-transform: uppercase;
    color: #5C6062;
  }

  &__action-item-submenu-links {
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid rgba(#272B38, .1);

    &:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom: none !important;
    }
  }

  &__action-item-submenu-link {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: #0123E7;
    letter-spacing: 0;
    text-decoration: none;
    text-transform: none;
    padding: 3px 0;
    transition: color .15s ease-in-out;

    &:hover {
      color: #00DB9C;
    }
  }

  .the-logo-inner .e-icon svg path:nth-child(2) {
    fill: #000;
  }

  &__in-dark {
    .the-logo-inner .e-icon svg path:nth-child(2) {
      fill: #fff;
    }

    #{$this}__logo-socials-item {
      background: transparent;
      border-color: rgba(92, 96, 98, 0.3);

      .e-icon svg path {
        fill: #8D9098;
      }

      &:hover {
        background: #101010;

        .e-icon svg path {
          fill: #fff;
        }
      }
    }

    #{$this}__search-selected-info-value {
      color: #8D9098;

      &.-price {
        color: #fff;
      }
    }

    #{$this}__search-selected-info-label {
      opacity: 1;
      color: $color-dark-grey;
    }

    #{$this}__nav {
      border-color: rgba(92,96,98,.3);
    }

    #{$this}__nav_white,
    #{$this}__action-item,
    #{$this}__wallet,
    #{$this}__buttons,
    #{$this}__switch-margin,
    #{$this}__fee-credit,
    #{$this}__wallet-list,
    #{$this}__search-input,
    #{$this}__select-contract {
      background: $dark-background-lighter;
      border-color: rgba(92,96,98,.3);
    }

    #{$this}__action-item {
      color: #fff;

      &.isOpen {
        background: #151924;
      }

      .e-icon svg,
      .e-icon svg path {
        fill: #fff;
      }
    }

    #{$this}__action-item-submenu {
      background: #151924;
      box-shadow: 0 12px 18px 0 rgba(17,17,17,0.60);
    }

    #{$this}__action-item-submenu-links {
      border-bottom: 1px solid #272B38;
    }

    #{$this}__action-item-submenu-link {
      color: #fff;

      &:hover {
        color: #00DB9C;
      }
    }

    #{$this}__fee-credit-value {
      color: #FEFEFE;
    }

    #{$this}__toggler,
    #{$this}__action-item,
    #{$this}__wallet,
    #{$this}__buttons,
    #{$this}__switch-margin,
    #{$this}__search {
      border-color: rgba(92,96,98,.3);
    }

    #{$this}__toggler:hover,
    #{$this}__action-item:hover {
      background: #151924;
    }

    #{$this}__toggler-text,
    .the-logo-inner__text,
    .the-logo-inner__version {
      color: #fff;
    }

    #{$this}__search-selected-token-code {
      opacity: 1;
      color: $color-dark-grey;
    }

    #{$this}__search-selected-name {
      color: $color-blue-dark-mode;
    }

    #{$this}__text {
      color: #8d9098;
    }

    #{$this}__search-selected-etherscan {
      color: $color-blue-dark-mode;

      .e-icon svg #e-icon-link2-ui-kit {
        fill: $color-blue-dark-mode;
      }
    }

    #{$this}__toggler-icon .e-icon svg {
      fill: #fff;
    }

    #{$this}__search-icon svg {
      fill: #8d9098;
    }

    #{$this}__search-close .e-icon svg {
      fill: #8D9098;
    }

    #{$this}__search {
      background: $dark-background-lighter;

      &.is-opened {
        background: $dark-background;
      }
    }

    #{$this}__buttons-green,
    #{$this}__buttons-opacity {
      color: #fff;
    }

    #{$this}__buttons-green {
      background-color: rgba(0,219,156,.2);

      &:hover {
        background-color: rgba(0,219,156,.3);
      }

      .green {
        color: #00DB9C;
      }
    }

    #{$this}__search-input {
      background: #232323;
      border-radius: 0;
      -webkit-appearance: none;
      color: $color-blue-dark-mode;
      caret-color: $color-blue-dark-mode;

      &::placeholder {
        color: $color-dark-grey;
      }

      &:hover {
        &::placeholder {
          color: $color-dark-grey;
        }

        + #{$this}__search-icon {
          svg {
            fill: $color-blue-dark-mode;
          }

          + #{$this}__search-selected {
            #{$this}__search-selected-name {
              color: $color-blue-dark-mode;
            }
          }
        }
      }

      &.is-opened,
      &:focus {
        background: #121212;

        + #{$this}__search-icon {
          svg {
            fill: $color-blue-dark-mode;
          }
        }
      }
    }

    .e-button_fill_green {
      color: #fff;
      padding-left: 10px;
      padding-right: 10px;
    }
    .e-button_fill_green-pale {
      padding-left: 10px;
      padding-right: 10px;
    }
    .e-button_fill_green-transparent {
      color: #fff;
      padding-left: 10px;
      padding-right: 10px;
    }

    #{$this}__buttons-switcher,
    #{$this}__fee-credit-value-wrapper {
      background: #343E4E;
    }

    #{$this}__fee-credit-value-wrapper {
      .e-tooltip__label {
        background: #596579;
        color: #fff;
      }
    }

    #{$this}__buttons-switcher-btn {
      color: #fff;

      &:hover {
        background: rgba(255,255,255,.07);
      }
    }

    #{$this}__switch-margin-label,
    #{$this}__select-contract-label,
    #{$this}__fee-credit-label {
      color: #d9d9d9;
    }

    #{$this}__select-contract-result,
    #{$this}__select-contract-options {
      background: rgba(57,64,80,1);
    }

    #{$this}__select-contract-wrapper {
      color: #fff;
    }
  }

  .search-enter-active, .search-leave-active {
    transition: all .2s ease-in-out;
  }
  .search-enter, .search-leave-to {
    transform: translate(0, -6px);
    opacity: 0;
  }
}
</style>




