      /*border-left: 1px solid rgba(#FFF, .4);*/
      padding: 0 48px;
      box-sizing: border-box;
      margin-left: -1px;

      @include r(767) {
        border-left: none;
        text-align: center;
        padding: 0 48px 0;
      }
    }
  }

  &__carousel-review-title {
    font-weight: 500;
    font-size: 20px;
    color: #112271;
    letter-spacing: 0;
    line-height: 26px;
    margin: 0 0 10px;
  }

  &__carousel-review-description {
    font-size: 14px;
    color: #00DB9C;
    letter-spacing: 0;
    margin: 0;

    .fade {
      opacity: 0.3;
      font-size: 11px;
      color: #112271;
      letter-spacing: 0;
    }
  }
}
</style>



